<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>
<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  export default {
    props: {
      probeType: {     // 1
        type: Number,
        default: 1
      },
      click: {      // fastclickclass="needsclick"
        type: Boolean,
        default: true
      },
      data: {
        type: Array,
        default: null
      },
      listenScroll: {
        type: Boolean,
        default: false
      }
    },
    mounted() {
      setTimeout(() => {    // 20domready
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {    // dom
          return
        }
        this.scroll = new BScroll(this.$refs.wrapper, {   // mountedbetter-scroll
          probeType: this.probeType,
          click: this.click
        })
        if (this.listenScroll) {
          let me = this
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
      },
      enable() {      // 
        this.scroll && this.scroll.enable()
      },
      disable() {     // 
        this.scroll && this.scroll.disable()
      },
      refresh() {     // 
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {    // 
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {   //  dom
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      data() {  // 
        setTimeout(() => {
          this.refresh()
        }, 20)
      }
    }
  }


</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>



